.Luxury {
    max-width: 1040px;
    margin: auto;
    padding: 5rem 0;
}

.Luxury .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    padding: 4rem 1rem;
}

.heading h2 {
    font-size: 4rem;
    text-align: center;
    padding: 2rem;
}

.heading p {
    text-align: center;
}

.text_bg {
    background: rgba(173, 199, 255, 0.502);
    height: 30px;
    max-width: 650px;
    margin: auto;
    position: absolute;
    left: 10px;
    right: 10px;
}

.text_bg p {
    font-size: 1.4rem;
    margin-top: -.8rem;
}

.card {
    width: 100%;
    border-radius: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.card:hover {
    cursor: pointer;
    color: #593cfb;
    transition: color .3s;
}

.card img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.card .content {
    text-align: center;
    padding: .5rem;
    font-size: 1rem;
}

@media screen and (max-width: 768px) {
    .Luxury .container {
        grid-template-columns: repeat(2, 1fr);
    }

    .heading h2 {
        font-size: 3rem;
    }
}

@media screen and (max-width: 500px) {
    .Luxury .container {
        grid-template-columns: 1fr;
    }
}

